.body{
  padding: 20px;
}

.blueGrid{
  display: grid;
  border: 5px solid blue;
}

.avatar{
  width: 100%;
  height: 40vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 10vh;
border-bottom: 1px solid orange;
}


.inputDiv{
  width: 90%;
  height: 20vh;
  border: 10px solid black;
  margin-top: 10vh;
}
.inputDiv textarea{
  width: 100%;
  height: 100%;
}

.header{
  margin-top: 10vh;
  border: 3px solid orange;
  padding: 10px;
}

.header a{
  text-decoration: none;
}

.textHeader{
  width: 100%;
  height: 6vh;
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid red;
}

.textAvatar{
  display: flex;
  align-items: center;
  gap: 8px;
}
.textAvatar img{
  height: 6vh;
}

.textEdit{
  display: grid;
  align-items: center;
}

