

/* LOADING  */
#loading {
  min-height: 5vh;
  background-image: linear-gradient(
    to right,
    white,
    silver,
    black
  );
  background-size: 200% 100%;
  animation: anim 1s linear infinite;
}

@keyframes anim{
  from{
      background-position: 50%;
    }
    to {
      background-position: -150%;
    }
}